html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  line-height: 1.15;
}

span {
  text-transform: capitalize;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 2;
}

a {
  text-decoration: none;
  background-color: transparent;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

[class*=contenedor] {
  max-width: 1200px;
  width: 95%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.card__price {
  color: #84d9bc;
  font-size: 6rem;
  font-weight: 900;
  line-height: 0;
}

.card__btn {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  text-transform: capitalize;
  background-color: #84d9bc;
  color: #000;
  font-weight: 700;
  margin-top: 2rem;
  padding: 1.2rem 4rem;
  transition: 0.3s all ease;
}
.card__btn:hover {
  color: rgb(93.0372670807, 204.9627329193, 166.7763975155);
  cursor: pointer;
}

[class*=title--dashedBlanco] {
  display: inline-block;
  font-weight: bold;
  margin: 0 0 2rem;
  position: relative;
  color: #313c4e;
  z-index: 1;
}
[class*=title--dashedBlanco]::after {
  content: "";
  display: block;
  min-width: 10rem;
  width: calc(100% + 1rem);
  background-color: #fff;
  height: 1rem;
  position: absolute;
  bottom: 4px;
  z-index: -1;
}

[class*=title--dashedVerde] {
  display: inline-block;
  font-weight: bold;
  margin: 0 0 2rem;
  position: relative;
  color: #313c4e;
  z-index: 1;
}
[class*=title--dashedVerde]::after {
  content: "";
  display: block;
  min-width: 10rem;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
  height: 1rem;
  position: absolute;
  bottom: 4px;
  z-index: -1;
}

.avif .header {
  background-image: linear-gradient(to right, rgba(57, 70, 89, 0.7), rgba(57, 70, 89, 0.7)), url(../img/header.avif);
}

.webp.nofavif .header {
  background-image: linear-gradient(to right, rgba(57, 70, 89, 0.7), rgba(57, 70, 89, 0.7)), url(../img/header.webp);
}

/* .notavif.notwebp*/
.header {
  background-image: linear-gradient(to right, rgba(57, 70, 89, 0.7), rgba(57, 70, 89, 0.7)), url(../img/header.jpg);
}

.header {
  padding: 5rem 0;
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
}
.header__logo {
  flex-basis: 50%;
}
.header__logo img {
  width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo img {
    margin-left: 0;
  }
}

.hero {
  padding: 15rem 0;
}
@media (min-width: 768px) {
  .hero__info {
    max-width: 50%;
  }
}
@media (min-width: 992px) {
  .hero__info {
    max-width: 40%;
  }
}
.hero__title {
  font-size: clamp(4.5rem, 6.7vw - 6.428px, 6rem);
  color: #fff;
  margin: 0 0 2rem;
  text-transform: capitalize;
}
.hero .card__btn {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  text-transform: capitalize;
  background-color: #84d9bc;
  color: #313c4e;
  font-weight: 700;
  margin-top: 2rem;
  padding: 1.2rem 4rem;
  transition: 0.3s all ease;
}
.hero .card__btn:hover {
  color: rgb(93.0372670807, 204.9627329193, 166.7763975155);
  cursor: pointer;
}
.hero .card__btn {
  width: 100%;
  margin-top: 0;
}
@media (min-width: 768px) {
  .hero .card__btn {
    width: auto;
  }
}

.crea {
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .crea {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}

.publicidad {
  background-color: #84d9bc;
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .publicidad__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    grid-template-columns: 1.5fr 1fr;
    align-items: center;
  }
}
.publicidad__img {
  transition: 0.3s transform;
}
.publicidad__img:hover {
  transform: rotate(8deg) scale(1.1);
}
.publicidad__title--dashedBlanco {
  margin-top: 5rem;
}

.estadisticas__contenedor {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .estadisticas__contenedor {
    padding: 8rem 0;
  }
}
.estadisticas__contenedor .estadisticas__title--dashedVerde {
  margin: 5rem 0 2rem;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .estadisticas__contenedor .estadisticas__title--dashedVerde {
    margin-top: 0;
  }
}